import permision from "@/common/permission.js"
export default {
    data () {
        return {
            title: 'scanCode',
            result: ''
        }
    },
    methods: {
        async scan () {






            uni.scanCode({
                success: (res) => {
                    this.result = res.result
                },
                fail: (err) => {

                    uni.getSetting({
                        success: (res) => {
                            let authStatus = res.authSetting['scope.camera'];
                            if (!authStatus) {
                                uni.showModal({
                                    title: '扫码失败',
                                    content: '扫码功能需要使用您的相机，请在设置界面打开相关权限',
                                    success: (res) => {
                                        if (res.confirm) {
                                            uni.openSetting()
                                        }
                                    }
                                })
                            }
                        }
                    })

                }
            });
        },






















    },
    mounted () {
        this.scan()
    },
}
